<template>

    <div v-if="rows.length">
      <Vue3EasyDataTable
        :headers="columns"
        :items="rows"
        fixed-header
        height="100vh"
        alternating
        border-cell
        buttons-pagination
        header-text-direction="center"
        body-text-direction="center"
        show-index
      >
      </Vue3EasyDataTable>
    </div>

</template>

<script>
import axios from "axios";
import Vue3EasyDataTable from "vue3-easy-data-table";
import "vue3-easy-data-table/dist/style.css";

export default {
  name: "StudentTable",
  components: {
    Vue3EasyDataTable,
  },
  props: {
    selectedMonth: {
      type: String,
      required: false,
      default: "",
    },
  },

  data() {
    return {
      columns: [
        { text: "이름", value: "stdName", width: 80, fixed: true },
        // { text: "수험 번호", value: "stdCode", width: 90 },
        { text: "분원", value: "stdClass", width: 70, sortable: true },
        { text: "계열", value: "classiFi", width: 80, sortable: true },
        { text: "문법", value: "engGram", width: 70, sortable: true },
        { text: "어휘", value: "engVoca", width: 70, sortable: true },
        { text: "논리", value: "engLogic", width: 70, sortable: true },
        { text: "독해", value: "engReading", width: 70, sortable: true },
        { text: "영어 점수", value: "engTotal", width: 90, sortable: true },
        { text: "영어 석차", value: "engRank", width: 90, sortable: true },

        { text: "미적분 1", value: "clCls1", width: 85, sortable: true },
        { text: "미적분 2", value: "clCls2", width: 85, sortable: true },
        { text: "미적분 3", value: "clCls3", width: 85, sortable: true },
        { text: "선형대수", value: "lnrAlgbr", width: 90, sortable: true },
        { text: "다변수", value: "mltvrClcls", width: 85, sortable: true },
        { text: "공학수학", value: "engnrMth", width: 90, sortable: true },
        { text: "수학 점수", value: "mathTotal", width: 90, sortable: true },
        { text: "수학 석차", value: "mathRank", width: 90, sortable: true },
      ],
      rows: [],
    };
  },

  // 데이터를 받아오는 코드는 한 번만 호출하도록 "created()" 훅에서 처리
  created() {
    this.fetchData();
  },

  watch: {
    // selectedMonth 변경 시 데이터를 갱신.
    selectedMonth() {
      this.fetchData();
    },
  },

  methods: {

    async fetchData() {
      try {
        const response = await axios.get(
          `https://raw.githubusercontent.com/Pwang-je/ScoreBoard/main/stuTable.json`);

        // console.log("API 응답 데이터:", response.data);
        // = response.data.OK

        // 선택한 월에 해당하는 데이터만 추출
        const selectedMonthData = response.data[this.selectedMonth];

        if (selectedMonthData) {
          // 데이터를 숫자로 변환하여 'rows' 데이터에 설정
          this.rows = selectedMonthData.map(item => ({
            ...item,
            engGram: parseFloat(item.engGram),
            engLogic: parseFloat(item.engLogic),
            engRank: parseFloat(item.engRank),
            engReading: parseFloat(item.engReading),
            engTotal: parseFloat(item.engTotal),
            engVoca: parseFloat(item.engVoca),

            clCls1: parseFloat(item.clCls1) || '',
            clCls2: parseFloat(item.clCls2) || '',
            clCls3: parseFloat(item.clCls3) || '',
            lnrAlgbr: parseFloat(item.lnrAlgbr) || '',
            mltvrClcls: parseFloat(item.mltvrClcls) || '',
            engnrMth: parseFloat(item.engnrMth) || '',
            mathTotal: parseFloat(item.mathTotal) || '',
            mathRank: parseFloat(item.mathRank) || ''
          }));
        } else {
          console.log(`Data not found for month: ${this.selectedMonth}`);
          this.rows = [];
        }

        console.log("type of response.data: ", typeof response.data);
        // = typeor reponse.data.OBJECT.OK

      } catch (error) {
        console.error("데이터를 가져오는 동안 오류가 발생했습니다:", error);
        // error handling,
        this.rows = [];
      }
    },

  },


};
</script>

<style>
.wrap-table {
  width: 1000px;
  height: 700px;
}

</style>
