<template>
  <div>
    <div>
      <Radar 
        v-if="loaded" 
        :data="chartData" 
        :options="radarChartOptions" 
        class="auto-height neu-flat" 
      />
    </div>

  </div>
</template>
<script>
import axios from "axios";
import { Radar } from "vue-chartjs";
import { Chart, registerables } from "chart.js";

Chart.register(...registerables);

export default {
  name: "RadarChart",
  components: { Radar },

  props: {
    selectedStudentName: {
      type: String,
      default: "",
      required: true,
    },
  },

  watch: {
    selectedStudentName(newSelectedStudentName) {
      console.log("raDar.vue selectedStudentName: ", newSelectedStudentName);
      this.getStudentData();
    },
  },

  data: () => ({
    loaded: false,
    chartData: "",
    radarChartOptions: {
      responsive: true,
      maintainAspectRatio: false,
      plugins: {
        legend: {
          position: 'top'
        },
        title: {
          display: false,
          text: 'comparison of scores by subject'
        }
      }
    },
  }),

  methods: {

    getStudentData() {
      axios
        .get(`https://raw.githubusercontent.com/Pwang-je/ScoreBoard/main/radarData.json`)
        .then((res) => {
          console.log("radar.vue selected student name = ", this.selectedStudentName);
          const studentData = res.data.find(student => student.name === this.selectedStudentName);

          if (studentData) {
            const { ttlgrScore, ttlvcScore, ttllgScore, ttlrdScore } = studentData;

            // console.log("ttlgrscore, ", ttlgrScore);
            // console.log("ttlvcScore, ", ttlvcScore);
            // console.log("ttllgScore, ", ttllgScore);
            // console.log("ttlrdScore, ", ttlrdScore);

            this.updateChartData(ttlgrScore, ttlvcScore, ttllgScore, ttlrdScore);
          } else {
            console.log("Student data not found for", this.selectedStudentName);
          }
        })
        .catch((error) => {
          console.log("error: ", error);
        });
    },


    updateChartData(ttlgrScore, ttlvcScore, ttllgScore, ttlrdScore) {
      this.chartData = {
        labels: ["문법", "어휘", "논리", "독해"],
        datasets: [
          {
            label: this.selectedStudentName,
            data: [ttlgrScore, ttlvcScore, ttllgScore, ttlrdScore],
            fill: true,
            backgroundColor: "rgba(54, 162, 235, 0.2)",
            borderColor: "rgb(54, 162, 235)",
            pointBackgroundColor: "rgb(54, 162, 235)",
            pointBorderColor: "#fff",
            pointHoverBackgroundColor: "#fff",
            pointHoverBorderColor: "rgb(54, 162, 235)",
          },
          {
            label: "전체 평균",
            // data: [ttlgrScore, ttlvcScore, ttllgScore, ttlrdScore],
            data: [11.6, 5.5, 7.3, 27.4],
            fill: true,
            backgroundColor: "rgba(255, 99, 132, 0.2)",
            borderColor: "rgb(255, 99, 132)",
            pointBackgroundColor: "rgb(255, 99, 132)",
            pointBorderColor: "#fff",
            pointHoverBackgroundColor: "#fff",
            pointHoverBorderColor: "rgb(255, 99, 132)",
          },
        ],
      };
      this.loaded = true;


    },


  },

  mounted() {
    if (this.selectedStudentName) {
      this.getStudentData();
    }
  },
};

</script>
<style>
.auto-height {
  height: 20rem;
}
.neu-flat {
  background: #f0f0f3;
  border-radius: 25px;
  box-shadow:
  -7px -7px 20px 0px #fff9,
  -4px -4px 5px 0px #fff9,
  7px 7px 20px 0px #0002,
  4px 4px 5px 0px #0001,
  inset 0px 0px 0px 0px #fff9,
  inset 0px 0px 0px 0px #0001,
  inset 0px 0px 0px 0px #fff9,        
  inset 0px 0px 0px 0px #0001;
}
.neu-pressed {
  background: #f0f0f3;
  border-radius: 25px;
  box-shadow:
  inset -7px -7px 20px 0px #fff9,
  inset -4px -4px 5px 0px #fff9,
  inset 7px 7px 20px 0px #0002,
  inset 4px 4px 5px 0px #0001,
  0px 0px 0px 0px #fff9,
  0px 0px 0px 0px #0001,
  0px 0px 0px 0px #fff9,        
  0px 0px 0px 0px #0001;
}
</style>