<template>

  <div class="container-fluid">

    <div class="row">

      <div class="d-flex justify-content-center">
        <select v-model="selectedMonth" class="form-select col">

            <option disabled value="">Select a month</option>
            <option
              v-for="(data, index) in monthlyData"
              :key="index"
              :value="data.month"
            >
              {{ data.month }}
            </option>
          
        </select>
      </div>
    

      <div class="col-md-6 col-lg-6">
        <Pie
          :data="selectedData"
          :options="pieChartOptions"
          v-if="selectedData !== null"
          :key="selectedMonth"
          class="chart-wrap neu-flat pd-pie"
        />
      </div>

      <div class="col-md-6 col-lg-6">
        <ScatterChart v-if="selectedMonth" :selected-month="selectedMonth" />
      </div>
    
    </div>

    <div class="row">
      <div class="tb-mg">
        <StuTable v-if="selectedMonth" :selected-month="selectedMonth" />
      </div>
      
    </div>

  </div>


</template>

<script>
import { Pie } from "vue-chartjs";
import { Chart, registerables } from "chart.js";
import ScatterChart from "@/components/scatters/sCatter.vue"; // will be change component.
import StuTable from "@/components/table/stuTable.vue";


Chart.register(...registerables);

export default {
  name: "PieChart",
  components: {
    Pie,
    ScatterChart,
    StuTable,
    
  },
  data() {
    const backgroundColors = [
      "rgb(255, 99, 132)",
      "rgb(255, 159, 64)",
      "rgb(255, 205, 86)",
      "rgb(75, 192, 192)",
      "rgb(54, 162, 235)",
      "rgb(63, 81, 181)",
      "rgb(153, 102, 255)",
      "rgb(255, 192, 203)",
      "rgb(165, 42, 42)",
      "rgb(201, 203, 207)",
    ];

    const labels = [
      "0-10",
      "11-20",
      "21-30",
      "31-40",
      "41-50",
      "51-60",
      "61-70",
      "71-80",
      "81-90",
      "91-100",
    ];

    const generateData = (data) => {
      return {
        labels: labels,
        datasets: [
          {
            data: data,
            backgroundColor: backgroundColors,
            hoverOffset: 4,
          },
        ],
      };
    };

    const februaryData = generateData([2, 2, 11, 18, 17, 25, 21, 16, 7, 2]);
    const MarchData = generateData([0, 9, 28, 38, 63, 52, 24, 17, 0, 0]);
    const AprilData = generateData([0, 11, 33, 43, 53, 38, 30, 17, 7, 1]);
    const MayData = generateData([0, 12, 23, 44, 44, 51, 29, 18, 4, 1]);
    const JuneData = generateData([0, 4, 10, 33, 47, 38, 28, 28, 5, 2]);
    const JulyData = generateData([0, 3, 15, 33, 39, 45, 26, 16, 5, 1]);
    const AugustData = generateData([0, 3, 15, 25, 49, 32, 30, 21, 17, 3]);
    const SeptemberData = generateData([0, 3, 12, 24, 38, 44, 35, 29, 4, 1]);
    // const OctoberData = generateData([]);
    // const NovemberData = generateData([]);

    return {
      pieChartOptions: {
        responsive: true,
        maintainAspectRatio: false,
        devicePixelRatio: 2,
        plugins: {
          legend: {
            position: 'top',
          },
          title: {
            display: true,
            text: 'distribution by each score'
          },
        },
      },
      selectedData: null,
      selectedMonth: "",
      monthlyData: [
        {
          month: "February",
          data: februaryData,
        },
        {
          month: "March",
          data: MarchData,
        },
        {
          month: "April",
          data: AprilData,
        },
        {
          month: "May",
          data: MayData,
        },
        {
          month: "June",
          data: JuneData,
        },
        {
          month: "July",
          data: JulyData,
        },
        {
          month: "Aug",
          data: AugustData,
        },
        {
          month: "Sep",
          data: SeptemberData,
        },
        // {
        //   month: "10월",
        //   data: OctoberData,
        // },
        // {
        //   month: "11월",
        //   data: NovemberData,
        // },
      ],
    };
  },

  watch: {
    selectedMonth(newVal) {
      const selected = this.monthlyData.find((data) => data.month === newVal);
      this.selectedData = selected ? selected.data : null;
    },
  },
};
</script>

