<script>
import axios from "axios";

export default {
  name: "studentStat",
  props: ["selectedStudentName"], // props

  data() {
    return {
      studentInfo: {
        name: "",
        halfPreviousYear: "",
        firstClass: "",
        scndClass: "",
        thirdClass: "",
        lastClass: "",
        div: "",
        affi: "",
      },
    };
  },

  watch: {
    selectedStudentName() {
      this.getStudentData(); // Get information when selectedStudentName changes.
    },
  },

  created() {
    this.getStudentData(); // Get student information when component is created.
  },

  methods: {
  
    async getStudentData() {
      try {
        const response = await axios
          .get(`https://raw.githubusercontent.com/Pwang-je/ScoreBoard/main/stuStat.json?name=${this.selectedStudentName}`)
        
        console.log('selectedStudentName : ', this.selectedStudentName);
        // find the student by name in the response data;
        const selectedStudent = response.data.find(student => student.name === this.selectedStudentName)

        if (selectedStudent) {
          this.studentInfo = selectedStudent;
        } else {
          console.error('student "${this.selectedStudentName}" not found.');
        }
        
          // this.studentInfo = response.data[0];
        // console.log("stustat-받아온 이름 ", this.selectedStudentName);
      } catch (error) {
        console.error("Error: ", error);
      } finally {
        this.loading = false;
      }
    }
  
  },
};
</script>

